import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import login from '@/views/login'
import warrant from '@/views/warrant'
import pay from '@/components/pay'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/warrant',
      name: 'warrant',
      component: warrant
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay
    }
  ]
})



